	/* повертає масив об'єктів, з властивостями потрібними для відображення посилань на статті */
	getLinkArticles(){  
		let link = this.articles.map(obj => {
			return {
				id: obj.id,
				url_name: obj.url_name,
				name: obj.name,
			}
		});

		return link;
	}

	/* повертає знайдену статтю по url_name */
	getArticlesByURL(url){
		let article = this.articles.find(obj => {
			return obj.url_name === url;
		});

		return article;
	}
